class Genre extends HTMLElement {
//   connectedCallback() {
//     this.render();
//   }

  set value(content) {
    this._content = content;
    this.render();
    this.afterRender();
    // console.log(content);
  }

  set title(title) {
    this._title = title;
  }

  render() {
    this.innerHTML = `
            <div class="genre bg-slate-800 px-2 py-2 mt-5 rounded">
                <div class="container w-full overflow-x-scroll flex items-center gap-2 scrollbar-hide scrollbar-custom md:pb-2">

                </div>
            </div>
        `;
  }

  afterRender() {
    const container = this.querySelector('.container');

    this._content.forEach((data) => {
      container.innerHTML += `<a class="bg-red-600 hover:bg-red-700 text-slate-50 text-sm font-medium py-1 px-2 rounded whitespace-nowrap transition-all" href="#/genre/${data.id}" title="${data.name}">${data.name}</a>`;
    });
  }
}

customElements.define('genre-tag', Genre);
