/* eslint-disable max-len */
/* eslint-disable class-methods-use-this */
import MoviesDB from '../script/data/MoviesDB';
import { headerTemplateCreator } from '../templates/templateCreator';

class Header extends HTMLElement {
  connectedCallback() {
    this.render();
    this.afterRender();
    this.buttonTrigger();
  }

  buttonTrigger() {
    const btnTrigger = this.querySelector('.buttonTrigger');
    const containerSearch = this.querySelector('.container-search');

    btnTrigger.addEventListener('click', () => {
      this.querySelector('input').focus();
      btnTrigger.classList.toggle('active');
      btnTrigger.classList.toggle('bg-slate-700');
      containerSearch.classList.toggle('w-0');
      containerSearch.classList.toggle('w-full');
      this.querySelector('.icon-search').classList.toggle('bi-search');
      this.querySelector('.icon-search').classList.toggle('bi-x-lg');
    });
  }

  render() {
    this.innerHTML = headerTemplateCreator();
  }

  async afterRender() {
    const inputSearch = this.querySelector('.search-input');
    const resultSearch = this.querySelector('.result');

    inputSearch.addEventListener('keyup', async () => {
      resultSearch.innerHTML = '';
      const resultData = await MoviesDB.Search(inputSearch.value);
      if (resultData < 1) {
        resultSearch.innerHTML = '<i>Result not found</i>';
        return;
      }
      resultData.forEach((data) => {
        resultSearch.innerHTML += `
          <a class="items mb-2 mr-2 bg-slate-900 rounded flex items-center gap-2 h-fit" href="#/${data.media_type}/${data.id}">
              <div class="poster w-[60px] h-full">
                <img class="object-fill h-full w-full rounded-tl-[inherit] rounded-bl-[inherit]" src="https://image.tmdb.org/t/p/w185${data.poster_path ? data.poster_path : data.profile_path}" alt="poster-of-${data.title}" loading="lazy">
              </div>
              <div class="title text-start leading-5 w-full">
                <h2 class="font-semibold text-sm">${data.title ? data.title : data.name}</h2>
                <i class="text-xs">${data.media_type.charAt(0).toUpperCase() + data.media_type.slice(1)}</i>
              </div>
          </a>
          `;
      });
    });
  }
}

customElements.define('header-tag', Header);
