class Footer extends HTMLElement {
  connectedCallback() {
    this.render();
  }

  render() {
    this.innerHTML = `
            <div class="w-full h-16 bg-slate-800 p-5 text-slate-50 text-xs text-center font-bold flex items-center justify-between gap-2">
                <p class="">RTH - Movies and TV Show Database</p>
                <p>&copy; Copyright 2023</p>
                <p class="">Supported by <a class="text-yellow-500 hover:text-yellow-600 transition-all" href="https://www.themoviedb.org/" rel="noopener noreferrer" target="_blank" title="The Movie Database">TMDB</a></p>
            </div>
        `;
  }
}

customElements.define('footer-tag', Footer);
